@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald:200&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
body {
  margin: 0;
  padding: 0;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-image: url(/React-Weather/static/media/sky.cd5032a5.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root {
  --family-font: "Roboto", sans-serif;
  --light-weight: "300";
  --normal-weight: "400";
  --title-weight: "900";
  --color: #fff;
  --bg-color: rgba(0, 0, 0, 0.75);
}

.row {
  display: flex;
  flex-direction: row;
}

.sectionTitle {
  font-family: "Roboto", sans-serif;
  font-family: var(--family-font);
  font-weight: "900";
  font-weight: var(--title-weight);
  color: #fff;
  color: var(--color);
}

.sectionText {
  font-family: "Roboto", sans-serif;
  font-family: var(--family-font);
  font-weight: "400";
  font-weight: var(--normal-weight);
  color: #fff;
  color: var(--color);
}

.sectionTextLight {
  font-weight: "300";
  font-weight: var(--light-weight);
  color: #fff;
  color: var(--color);
}

.container {
  background-color: rgba(0, 0, 0, 0.75);
  background-color: var(--bg-color);
  width: 85vw;
  height: 85vh;
  -webkit-animation: appear 400ms;
          animation: appear 400ms;
}

@-webkit-keyframes appear-slide {
  0% {
    -webkit-transform: translateX(-15%);
            transform: translateX(-15%);
    opacity:0;
  }
  
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity:1;
  }

}

@keyframes appear-slide {
  0% {
    -webkit-transform: translateX(-15%);
            transform: translateX(-15%);
    opacity:0;
  }
  
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity:1;
  }

}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.Temperature {
