.TemperatureIcon {
  height: 50px;
  -webkit-animation: appear 1000ms;
          animation: appear 1000ms;
}

.TemperatureInfoContainer {
  width: 500px;
   -webkit-animation: appear-slide 4s;
           animation: appear-slide 4s;
  -webkit-animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
          animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.TemperatureInfo span {
  display: flex;
  align-items: flex-end;
  margin: 15px;
}

.row .sectionText,
.row .sectionTitle {
  margin: 5px 0 10px 25px;
  overflow: hidden;
}

.TemperatureInfoTitle {
  font-size: 2em;
  font-weight: 200;
